import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';

import { Row, Card, Form, Input } from 'antd';

import Page from 'components/LayoutComponents/Page';
import BtnSave from 'components/common/button/BtnSave';
import BtnCancel from 'components/common/button/BtnCancel';

import { studentTitles } from 'dump/data';

@Form.create({ name: 'formSpcializeTraining' })
class FormStudentTitlesPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      specializeTrainingId: null,
      initialValue: {},
    };
    this.updateTitle ='Cập nhật chức danh sinh viên';
    this.addNewTitle = 'Thêm chức danh sinh viên';
  }

  componentDidMount = () => {
    const { match } = this.props;
    if (match.params.id) {
      this.setState({ specializeTrainingId: match.params.id });
      this.getSpecializeTrainingById(match.params.id);
    }
  };

  getSpecializeTrainingById = id => {
    const SpecializeTrainingByID = studentTitles.filter(specializedTraining => specializedTraining.code === id)[0];
    const initialValue = this.handleInitialValue(SpecializeTrainingByID);
    this.setState({ initialValue });
  };

  handleInitialValue = initialValue => {
    const initialValueDefault = {
      trainingIndustryCode: '',
      code: '',
      name: '',
      nameEn: '',
    };
    return { ...initialValueDefault, ...initialValue };
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };

  render() {
    const { initialValue } = this.state;
    const { getFieldDecorator } = this.props.form;
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 5 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 12 },
      },
      colon: false,
    };
    const { specializeTrainingId } = this.state;
    return (
      <Card title={specializeTrainingId ? this.updateTitle : this.AddNewString}>
        <Row>
          <Form {...formItemLayout} onSubmit={this.handleSubmit} className="login-form">
            <Form.Item {...formItemLayout} label="Tên chức danh" hasFeedback>
              {getFieldDecorator('name', {
                initialValue: initialValue.name,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label="Mã chức danh" hasFeedback>
              {getFieldDecorator('code', {
                initialValue: initialValue.code,
                rules: [
                  {
                    required: true,
                    message: 'Vui lòng nhập thông tin!',
                  },
                ],
              })(<Input />)}
            </Form.Item>
            <Form.Item {...formItemLayout} label=" ">
              <BtnSave htmlType="submit" />
              <BtnCancel />
            </Form.Item>
          </Form>
        </Row>
      </Card>
    );
  }
}

export default withRouter(props => (
  <Page {...props}>
    <FormStudentTitlesPage {...props} />
  </Page>
));



